/* style all elements with a required attribute */
input, select {
    &:invalid:required {
        border-right: #d2179c 3px solid;

        .custom-checkbox & {
            ~ .custom-control-label:after {
                content: " *";
                color:#d2179c;
                    left: unset;
                    right: -18px;
                    top: 2px;
            }
        }
    }
}

.custom-control-label {
    padding-top: 3px;

    [dir="rtl"] & {
        &:before,
        &:after {
            right: -22px;
        }
    }

    .custom-control-inline & {
        padding-top: 0px;
    }
}
