.lw-phone-call-container {
    width: 100px;
    top: 120px;
    left: calc(50% - 50px);
    position: absolute;
    height: 100px;
    display: flex;
    margin: auto;

    .lw-phone-call {
        position: absolute;
        background:none;
        border-radius: 50%;
        border: 2px dotted #d2179c;

        animation-name:lw-phone-waves;
        animation-iteration-count: infinite;
        animation-direction: normal;
        animation-duration:1.5s;

        &-1{
            animation-delay:0.2s;
        }
        &-2 {
            animation-delay:0.4s;
        }
        &-3{
            animation-delay:0.6s;
        }

        &-4{
        animation-delay:0.8s;
        }
    }
}


@-webkit-keyframes lw-phone-waves {
    0%{
        width: 10%;
        height: 10%;
        left: 45%;
        top: 45%;
        opacity: 0;
    }
    10%{
        opacity:1;
    }
    100%{
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        opacity: 0;
    }
}


