    .btn.lw-group-radio-option-img {
        width: 40%;
    }    
}

@media (max-width: 767px) {
    .btn-block-on-mobile {
        width: 100%;
    }
    .btn.lw-group-radio-option-img {
        width: 100%;
    }

    .lw-basic-filter-field {
        width: 100%;
        margin-bottom: 8px;
        input.form-control,
        select.form-control {
            width: 100%;
        }
        
        select {
            margin-bottom: 4px;
        }
    }

    .lw-basic-filter-container {
        min-width: auto;
    }

    .topbar .dropdown .dropdown-menu.lw-basic-filter-container {
        width: 100%;
    }

    .lw-basic-filter-footer-container {
        .btn {
            margin-bottom: 6px;
        }
    }

    div.dataTables_wrapper div.dataTables_paginate {
        ul.pagination {
            justify-content: center;
            margin-top: 16px;
        }
        .paginate_button {
            display: none;
            &.next,
            &.previous{
                display: inline-block;
            }
        }
    }

    .lw-horizontal-container .lw-user-photo {
        height: auto;
        width: 100%;
    }

    .lw-user-gift-container {
        width: 100%;
        height: auto;
        padding-bottom: 16px;
        &:before {
            margin-left: calc(50% - 13px);
        }
    }
}